      </svg>
      <div class="container">
        <div class="checkout-addr">
          <div class="page-title-normal">
            <h2 class="page-title-h2">
              <span>check out</span>
            </h2>
          </div>
          <!-- process step -->
          <div class="check-step">
            <ul>
              <li class="cur">
                <span>Confirm</span> address
              </li>
              <li>
                <span>View your</span> order
              </li>
              <li>
                <span>Make</span> payment
              </li>
              <li>
                <span>Order</span> confirmation
              </li>
            </ul>
          </div>

          <!-- address list -->
          <div class="page-title-normal checkout-title">
            <h2>
              <span>Shipping address</span>
            </h2>
          </div>
          <div class="addr-list-wrap">
            <div class="addr-list">
              <ul>
                <li v-for="(item,index) in addressListFilter" :key=index :class="{'check':checkIndex==index}" @click="checkIndex=index;orderAddressId=item.addressId">
                  <dl>
                    <dt>{{item.userName}}</dt>
                    <dd class="address">{{item.streetName}}</dd>
                    <dd class="tel">{{item.tel}}</dd>
                  </dl>
                  <div class="addr-opration addr-del" @click="getAddressId(item)"> 
                    <a href="javascript:;" class="addr-del-btn">
                      <svg class="icon icon-del">
                        <use xlink:href="#icon-del"></use>
                      </svg>
                    </a>
                  </div>
                  <div class="addr-opration addr-set-default">
                    <a href="javascript:;" class="addr-set-default-btn" v-show="!item.isDefault"  @click="setDefault(item)">
                      <i>Set default</i>
                    </a>
                  </div>
                  <div class="addr-opration addr-default" v-show="item.isDefault">Default address</div>
                </li>
                <li class="addr-new">
                  <div class="add-new-inner">
                    <i class="icon-add">
                      <svg class="icon icon-add">
                        <use xlink:href="#icon-add"></use>
                      </svg>
                    </i>
                    <p>Add new address</p>
                  </div>
                </li>
              </ul>
            </div>

            <div class="shipping-addr-more">
              <a class="addr-more-btn up-down-btn" href="javascript:;" @click="expend" :class="{'open':limit>3}">
                more
                <i class="i-up-down">
                  <i class="i-up-down-l"></i>
                  <i class="i-up-down-r"></i>
                </i>
              </a>
            </div>
          </div>

          <!-- shipping method-->
          <div class="page-title-normal checkout-title">
            <h2>
              <span>Shipping method</span>
            </h2>
          </div>
          <div class="lemall-msg-info hidden">
            <span>The region you selected is not within our delivery area. Please select another shipping address within our delivery areas.</span>
          </div>
          <div class="shipping-method-wrap">
            <div class="shipping-method">
              <ul>
                <li class="check">
                  <div class="name">Standard shipping</div>
                  <div class="price">Free</div>
                  <div class="shipping-tips">
                    <p>Once shipped，Order should arrive in the destination in 1-7 business days</p>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <div class="next-btn-wrap">
            <router-link class="btn btn--m btn--red" :to="{path:'/orderlist',query:{addressId:orderAddressId}}">Next</router-link>
          </div>
        </div>
      </div>
    </div>
    <model :mdShow="isShowDelAddressModel" @closeModel="closeDelAddressModel">
      <p slot="content">
        确认要删除这个地址吗？
      </p>
      <div slot="btn">
         <a class="btn btn--m" href="javascript:;" @click="delAddress">确认删除</a>
        <a class="btn btn--m btn--red" href="javascript:;" @click="closeDelAddressModel">取消</a>
      </div>
    </model>
    <nav-footer></nav-footer>
  </div>
</template>
<script>
import NavHeader from "@/components/NavHeader";
import NavFooter from "@/components/NavFooter";
import NavBread from "@/components/NavBread";
import Model from "@/components/Model";
import axios from "axios";

export default {
  data() {
    return {
        addressList:[],
        limit:3,
        checkIndex:0,
        isShowDelAddressModel:false,
        delAddressId:'',
        orderAddressId:''
    };
  },
  components: {
    NavHeader,
    NavFooter,
    NavBread,
    Model
  },
  mounted(){
    this.init();
  },
  computed:{
    addressListFilter(){
      return this.addressList.slice(0,this.limit)
    }
  },
  methods:{
    init(){
      axios.get('/users/address').then((res)=>{
        if(res.data.status == '0'){
          this.addressList = res.data.result;
          this.addressList.forEach((item,index)=>{
            if(item.isDefault===true){
              this.checkIndex = index;
            }
          })
        }
        // console.log(this.addressList);
      })
    },
    expend(){
      if(this.limit === 3){
        this.limit = this.addressList.length;
      }else{
        this.limit = 3;
      }
    },
    setDefault(item){
      axios.post('/users/setDefaultAddress',{
        addressId:item.addressId
      }).then((res)=>{
        if(res.data.status === '0'){
          this.init()
        }
      })
    },
    delAddress(){
      axios.post('/users/delAddress',{
        addressId:this.delAddressId
      }).then((res)=>{
        if(res.data.status === '0'){
          this.init()
          this.isShowDelAddressModel = false;
        }
      })
    },
    closeDelAddressModel(){
      this.isShowDelAddressModel = false;
    },
    getAddressId(item){
      this.isShowDelAddressModel = true;
      this.delAddressId = item.addressId;
      console.log(this.delAddressId)
    }
  }
};
</script>



